.MainBody {
    height: 100vh;
    width: 100%;
    background-image: url(Images/bgbgbg.png);
    background-position: center;
    display: flex;
    align-items: center;
    transition: justify-content 0.3s ease-in-out; 
}

.align-start {
    justify-content: start;
}

.align-end {
    justify-content: end;
}

.PhoneDiv {
    width: 28.16rem;
    height: 48.1194rem;
    background-image: linear-gradient(white, rgb(195, 223, 243));
    grid-area: 1 / 1 / 2 / 2;
    z-index: 1;
    border-radius: 25px; 
    box-shadow: 0 20px 20px -4px rgba(0, 0, 0, 0.2), 28px 0 10px -10px rgba(0, 0, 0, 0.2), -18px 0 18px -18px rgba(0, 0, 0, 0.2);
}

.WavePicPhone {
    background-image: url('Images/waves.png');
    background-repeat: no-repeat;
    background-size: cover;
    height: 200px;
}

#signup-first-name,
#signup-last-name,
#signup-email,
#signup-phone,
#signup-dob,
#signup-password,
#signup-confirmpassword,
#login-password,
#login-phone {
    background: transparent;
    placeholder {
        color: black;
    }
}

.user-sign-text {
    text-align: center;
    padding-top: 1rem;
    text-shadow: 2px 2px 4px #777777;
}

.SubtractDiv {
    width: 18rem;
    height: 55px;
    background-color: white;
    border-bottom: 1px solid rgb(189, 189, 189);
    grid-area: 1 / 1 / 1 / 2;
    z-index: 2;
    position: relative;
    top: -35px; /* Move the small div up to overlap */
    left: 5rem; /* Center the small div horizontally */
    border-radius: 8px;
}

.SubtractDiv::after {
    content: '';
    position: absolute;
    top: 50%;
    right: 0;
    width: 1px;
    height: 40%;
    background-color: rgb(198, 198, 198);
    opacity: 30%;
}

.SubtractDiv::before {
    content: '';
    position: absolute;
    top: 50%;
    left: 0;
    width: 1px;
    height: 40%;
    background-color: rgb(198, 198, 198);
    opacity: 30%;
}

#signup-dob {
    color: grey;
}
